$color: rgb(222, 226, 235);
$color-dark: rgb(22, 23, 24);
$font-primary: "latoregular", sans-serif;

@font-face {
    font-family: "latoregular";
    src: url("../fonts/lato-regular-webfont.woff2") format("woff2"), url("../fonts/lato-regular-webfont.woff") format("woff");
    font-weight: normal;
    font-style: normal;
}

body {
    background: $color;
}

p {
    font-family: "latoregular", sans-serif;
    color: $color-dark;
}



