a,
a:focus {
    outline: none !important;
    box-shadow: none !important;
    transform: translateX(12px);
}

.blog-main .article {
    flex: 0 0 47.5%;
    max-width: 47.5%;
    margin: 0 1.25%;
}

.blog-main a {
    text-decoration: none;
}

.blog-main .badge {
    font-size: 1rem;
}

.blog-sidebar > div {
    background: url("../images/bg.png");  // image from www.subtlepatterns.com
}

.blog-sidebar a {
    color: white;
}

.pagination span {
    border: none;
}

.error404 .container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: calc(100vh - (56px + 32px));
    margin-top: 0 !important;
}

.error404 h1 {
    font-size: 5rem;
}



